/* General (Landing Page) Styles */
body, .app-container.general {
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(135deg, #181a1b 0%, #232526 100%);
  min-height: 100vh;
  color: #e0e7ef;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.landing-container {
  text-align: center;
  padding: 3rem 1rem;
}

.room-list {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
}

.room-card {
  background: rgba(30, 32, 40, 0.95);
  border-radius: 12px;
  padding: 2rem;
  min-width: 220px;
  box-shadow: 0 0 24px #0008, 0 0 8px #222;
  border: 2px solid #232526;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.3s;
  position: relative;
}

.room-card h2 {
  margin-bottom: 0.5rem;
  color: #e0e7ef;
}

.room-card button {
  margin-top: 1rem;
  background: #232526;
  color: #e0e7ef;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1.2rem;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 0 8px #0004;
  transition: background 0.2s, color 0.2s;
}

.room-card button:hover {
  background: #444;
  color: #fff;
}

.difficulty-label {
  position: absolute;
  left: 18px;
  bottom: 14px;
  font-size: 0.95em;
  color: #b0c4d8;
  opacity: 0.85;
  letter-spacing: 1px;
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
  background: rgba(20,30,50,0.7);
  padding: 0.18em 0.9em;
  border-radius: 1em;
  z-index: 3;
  box-shadow: 0 0 4px #00ffe733;
  border: 1px solid #233;
  pointer-events: none;
  transition: background 0.2s, color 0.2s;
}
.room-card .difficulty-label {
  font-weight: 500;
}

/* Futura Sci-Fi Room Card on Landing Page */
.sci-fi-room-card {
  position: relative;
  overflow: hidden; /* Ensures overlay stays inside card */
  background: rgba(20, 30, 50, 0.85);
  border-radius: 18px;
  box-shadow: 0 0 32px #00ffe7aa, 0 0 8px #0ff;
  border: 2px solid #00ffe7;
  color: #e0e7ef;
  font-family: 'Orbitron', 'Segoe UI', Arial, sans-serif;
  text-shadow: 0 0 8px #00ffe7, 0 0 2px #fff;
  z-index: 1;
}

.card-grid-overlay {
  position: absolute;
  top: 0; left: 0; width: 100%; height: 100%;
  z-index: 1;
  pointer-events: none;
}

.sci-fi-room-card-content {
  position: relative;
  z-index: 2;
}

.sci-fi-room-card h2 {
  color: #00ffe7;
  text-shadow: 0 0 4px #00ffe7;
}

.sci-fi-room-card .sci-fi-btn {
  margin-top: 1rem;
}

.futura-intro-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2.5rem 1.5rem 2rem 1.5rem;
  background: rgba(20, 30, 50, 0.95);
  border-radius: 18px;
  box-shadow: 0 0 32px #00ffe7aa, 0 0 8px #0ff;
  max-width: 440px;
  margin: 2.5rem auto;
  position: relative;
  z-index: 2;
}

.futura-intro-content h1 {
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-family: 'Orbitron', 'Segoe UI', Arial, sans-serif;
  color: #00ffe7;
  text-shadow: 0 0 8px #00ffe7, 0 0 2px #fff;
  font-size: 2.1rem;
  letter-spacing: 2px;
  text-align: center;
}

.futura-intro-content p {
  color: #e0e7ef;
  font-size: 1.15rem;
  margin-bottom: 2rem;
  text-align: center;
}

.futura-intro-content .intro-image {
  width: 130px;
  height: 130px;
  object-fit: contain;
  border-radius: 50%;
  box-shadow: 0 0 24px #00ffe7cc, 0 0 4px #fff;
  background: #101c2c;
  margin-bottom: 0.5rem;
  margin-top: 10px;
  border: 3px solid #00ffe7;
}

/* Sci-Fi (Futura Room) Styles */
.app-container.sci-fi {
  font-family: 'Orbitron', 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(135deg, #0f2027 0%, #2c5364 100%);
  min-height: 100vh;
  color: #e0e7ef;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  position: relative;
  z-index: 1;
}

.sci-fi-buttons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin: 2rem 0 2.5rem 0; /* top and bottom margin */
}

.sci-fi-btn {
  padding: 0.6rem 1.5rem;
  border-radius: 8px;
  border: none;
  background: linear-gradient(90deg, #00ffe7 0%, #0ff 100%);
  color: #222;
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  box-shadow: 0 0 16px #00ffe7cc, 0 0 2px #fff;
  letter-spacing: 1px;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s, transform 0.1s;
  margin: 0;
}

.sci-fi-btn:hover {
  background: linear-gradient(90deg, #0ff 0%, #00ffe7 100%);
  color: #111;
  box-shadow: 0 0 24px #00ffe7ee, 0 0 4px #fff;
  transform: translateY(-2px) scale(1.04);
}

.sci-fi-btn-secondary {
  background: #232526;
  color: #fff;
  box-shadow: 0 0 16px #ff00cc99, 0 0 2px #fff;
}

.sci-fi-btn-secondary:hover {
  background: linear-gradient(90deg, #ff00cc 0%, #a259ff 100%);
  color: #fff;
  box-shadow: 0 0 24px #ff00ccee, 0 0 4px #fff;
  transform: translateY(-2px) scale(1.04);
}

.app-container.sci-fi h1, 
.app-container.sci-fi h2, 
.app-container.sci-fi h4 {
  letter-spacing: 2px;
  text-shadow: 0 0 8px #00ffe7, 0 0 2px #fff;
}

.puzzle-box {
  background: rgba(20, 30, 50, 0.85);
  border-radius: 18px;
  padding: 0 2rem 2rem 2rem;
  padding-top: 0;
  margin: 2rem auto;
  max-width: 700px;
  box-shadow: 0 0 32px #00ffe7aa, 0 0 8px #0ff;
  border: 2px solid #00ffe7;
  position: relative;
  z-index: 1;
  transition: box-shadow 0.3s, border 0.3s, background 0.3s, opacity 0.5s;
  opacity: 1;
  overflow: hidden; 
}

.puzzle-box.fade-out {
  opacity: 0;
  pointer-events: none;
}

.puzzle-content {
  margin-bottom: 1.5rem;
}

.puzzle-image {
  max-width: 100%;
  border-radius: 10px;
  margin-bottom: 1rem;
  box-shadow: 0 0 16px #00ffe7cc;
  border: 1.5px solid #00ffe7;
}

.answer-form {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.answer-form input {
  padding: 0.6rem;
  border-radius: 6px;
  border: 1.5px solid #00ffe7;
  background: #101c2c;
  color: #fff;
  width: 60%;
  font-size: 1.1rem;
  outline: none;
  box-shadow: 0 0 8px #00ffe7aa;
  transition: border 0.2s;
}

.answer-form input:focus {
  border: 1.5px solid #0ff;
}

.answer-form button {
  padding: 0.6rem 1.2rem;
  border-radius: 6px;
  border: none;
  background: linear-gradient(90deg, #00ffe7 0%, #0ff 100%);
  color: #222;
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  box-shadow: 0 0 12px #00ffe7cc;
  transition: background 0.2s, color 0.2s;
}

.answer-form button:hover {
  background: linear-gradient(90deg, #0ff 0%, #00ffe7 100%);
  color: #111;
}

.message {
  margin: 1rem 0;
  font-weight: bold;
  color: #ff00cc;
  text-shadow: 0 0 8px #ff00cc88;
}

.hint-area {
  background: rgba(10, 20, 40, 0.85);
  border-radius: 10px;
  padding: 1rem;
  margin-top: 1rem;
  text-align: left;
  border: 1.5px solid #00ffe7;
  box-shadow: 0 0 8px #00ffe788;
}

.hint-area h4 {
  margin: 0 0 0.5rem 0;
  color: #00ffe7;
  text-shadow: 0 0 4px #00ffe7;
}

.hint-area ul {
  list-style: disc inside;
  margin: 0 0 0.5rem 0;
  padding: 0;
}

.hint-area button {
  background: #232526;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 0.4rem 1rem;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 0 8px #00ffe7cc;
  margin-top: 0.5rem;
}

.hint-area button:hover {
  background: linear-gradient(90deg, #0ff 0%, #00ffe7 100%);
  color: #111;
}

.progress {
  margin-top: 2rem;
  color: #00ffe7;
  text-shadow: 0 0 4px #00ffe7;
  font-size: 1.1rem;
  text-align: center;           /* Add this line */
  display: flex;                /* Add this line for flex centering */
  flex-direction: column;
  justify-content: center;      /* Add this line for flex centering */
  align-items: center;          /* Optional: vertical centering if needed */
}

.progress .sci-fi-btn {
  margin-top: 1.5rem;
}

/* Text puzzle */
.text-puzzle {
  margin: 0 auto;
  max-width: 600px;
  text-align: left;
}

.riddle-number {
  color: #00ffe7;
  font-weight: bold;
  margin-bottom: 0.5rem;
  margin-top: 1rem;
}

.riddle-question {
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

/* Image puzzle */

.flashlight-image-container {
  position: relative;
  width: calc(100% + 4rem);
  margin-left: -2rem;
  margin-right: -2rem;
  border-radius: 10px 10px 0 0;
  overflow: hidden;
  background: red;
}

.flashlight-image-container img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 10px 10px 0 0;
  box-shadow: 0 0 16px #00ffe7cc, 0 0 8px #00ffe7aa;
  margin: 0;
}


.flashlight-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-radius: 10px;
  transition: background 0.3s; /* default transition */
}

.flashlight-overlay.fast {
  transition: background 0.1s;
}

.flashlight-overlay.slow {
  transition: background 0.3s;
}

.interactive-area {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 1rem 0;
}

/* Flora, Slime, or Critter */
.card-grid,
.chambers {
  width: 100%;
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  gap: 0.7rem;
  flex-wrap: wrap;
}

.card-3d {
  width: 80px;
  height: 120px;
  perspective: 600px;
  border-radius: 12px;
  position: relative;
  transition: opacity 0.2s;
  margin-bottom: 1rem;
}

.card-inner {
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 0.5s cubic-bezier(.4,2,.6,1);
  transform-style: preserve-3d;
}

.card-3d.flipped .card-inner {
  transform: rotateY(180deg);
}

.card-front, .card-back-face {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-front {
  background: linear-gradient(135deg, #232526 0%, #0f2027 100%);
  box-shadow: 0 0 8px #00ffe7aa;
}

.card-back-face {
  background: #222;
  box-shadow: 0 0 16px #a259ffcc, 0 0 8px #00ffe7cc;
  transform: rotateY(180deg);
  flex-direction: column;
  padding: 0;
  overflow: hidden;
}

.card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
  display: block;
}

.card-3d.sorted .card-inner {
  filter: grayscale(0.7) brightness(0.7);
}

.chambers {
  width: 100%;
  max-width: 480px;
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.chamber.chamber-img {
  width: 120px;
  height: 120px;
  background: rgba(20, 30, 50, 0.85);
  border-radius: 16px;
  box-shadow: 0 0 16px #00ffe7cc;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: box-shadow 0.2s, border 0.2s;
  border: 2.5px solid #00ffe7;
  position: relative;
}

.chamber-icon {
  width: 100px;
  height: 100px;
  object-fit: contain;
  filter: drop-shadow(0 0 8px #00ffe7cc);
}

@media (max-width: 600px) {
  .card-3d {
    width: 60px;
    height: 90px;
    border-radius: 8px;
  }
  .chamber.chamber-img {
    width: 60px;
    height: 60px;
    border-radius: 10px;
  }
  .chamber-icon {
    width: 36px;
    height: 36px;
  }
  .card-img {
    border-radius: 8px;
  }
  .card-grid, .chambers {
    max-width: 320px;
    gap: 0.4rem;
  }
}

.chamber-correct {
  box-shadow: 0 0 32px #00ffb3cc, 0 0 16px #00ffe7cc;
  border-color: #00ffb3;
  animation: chamber-glow-correct 0.7s;
}

.chamber-incorrect {
  box-shadow: 0 0 32px #ff00cccc, 0 0 16px #a259ffcc;
  border-color: #ff00cc;
  animation: chamber-glow-incorrect 0.7s;
}

@keyframes chamber-glow-correct {
  0% { box-shadow: 0 0 0 #00ffb3cc; }
  50% { box-shadow: 0 0 32px #00ffb3cc, 0 0 16px #00ffe7cc; }
  100% { box-shadow: 0 0 0 #00ffb3cc; }
}

@keyframes chamber-glow-incorrect {
  0% { box-shadow: 0 0 0 #ff00cccc; }
  50% { box-shadow: 0 0 32px #ff00cccc, 0 0 16px #a259ffcc; }
  100% { box-shadow: 0 0 0 #ff00cccc; }
}

.sort-progress {
  text-align: center;
  color: #00ffe7;
  margin-top: 1rem;
  font-size: 1.1rem;
}

.hotspot-btn {
  position: absolute;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  z-index: 2;
  outline: none;
  transition: box-shadow 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hotspot-btn.found .hotspot-icon {
  filter: grayscale(0.7) brightness(0.7);
  opacity: 0.7;
}

.hotspot-btn.hotspot-hidden {
  border: 1px dashed rgba(200,220,255,0.25) !important;
  /* Or use a solid border, but very faint: */
  /* border: 1px solid rgba(200,220,255,0.08) !important; */
  box-shadow: none;
}

.hotspot-btn.hotspot-disabled {
  cursor: default !important;
  pointer-events: none;
  opacity: 0.8; 
}

.hotspot-icon {
  width: 100%;
  height: 100%;
  object-fit: contain;
  pointer-events: none;
  display: block;
}

.clue-modal {
  position: fixed;
  top: 0; left: 0; width: 100vw; height: 100vh;
  background: rgba(10,20,40,0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.clue-content {
  background: #222;
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 0 32px #00ffe7aa;
  color: #fff;
  max-width: 60vw;
  max-height: 80vh;
  overflow: auto;
  text-align: center;
}

.clue-content img {
  border-radius: 8px;
  box-shadow: 0 0 12px #ff1744cc;
  max-width: 120px;
  margin: 1rem auto;
  display: block;
}

.bridge-puzzle {
  position: relative;
  width: 100%;
  height: auto;
  padding: 0;
  margin: 0;
}

.bridge-image {
  width: calc(100% + 4rem);
  margin-left: -2rem;
  margin-right: -2rem;
  border-radius: 16px 16px 0 0;
  display: block;
  box-shadow: none;
  height: auto;
}

.final-futura-success {
  color: #fff;
  text-shadow:
    0 0 8px #ff00cc,
    0 0 16px #ff00cc,
    0 0 32px #ff00cc88,
    0 0 2px #fff;
  font-family: 'Orbitron', 'Segoe UI', Arial, sans-serif;
  letter-spacing: 2px;
  font-size: 2.2rem;
  text-align: center;
  animation: futura-pulse-shadow 2s infinite alternate;
}

@keyframes futura-pulse-shadow {
  0% {
    text-shadow:
      0 0 8px #ff00cc,
      0 0 16px #ff00cc,
      0 0 32px #ff00cc88,
      0 0 2px #fff;
  }
  100% {
    text-shadow:
      0 0 32px #ff00cc,
      0 0 64px #ff00cc,
      0 0 128px #ff00cc,
      0 0 8px #fff;
  }
}